<template>
    <div class="board frame_mine">
        <div class="board my_info" :style="'background-image:url(' + bgImg + ')'">
            <div id="user_head_img1" :style="'opacity: ' + headImgShow + ';background-image:url(' + userImg + ')'" :class="headImgSwing ? ' ' : 'stop_amt'"></div>
            <input id="set_head_bg" type="file" name="bg" @change="setBG($event)" />
            <input id="set_head_img" @change="setImg($event)" type="file" name="head" />
        </div>
        <div class="board white">
            <div class="server_list">个人信息</div>
            <div class="user_acc">
                <span class="bold">用户：</span>
                <span class="c999">{{userNum()}}</span>
            </div>
            <div class="user_acc">
                <span class="bold">昵称：</span>
                <span class="c999">{{nickname}}</span>
            </div>
            <div class="user_acc">
                <span class="bold">个人余额：</span>
                <span class="c999">{{money}}</span>
            </div>
            <div class="user_acc">
                <span class="bold">注册时间：</span>
                <span class="c999">{{userTime()}}</span>
            </div>
            <div class="user_acc">
                <span class="bold">本次登录：</span>
                <span class="c999">{{loginTime}}</span>
            </div>
        </div>
        <div class="board white">
            <div class="server_list">功能</div>
            <ul class="board">
                <li v-for="(item, index) in servers" :key="index" @click="setFn(index)">
                    <img :src="servers[index].icon" width="50%" height="auto" />
                    <p>{{servers[index].name}}</p>
                </li>
            </ul>
            <div class="bgm" v-show="bgm">
                <p>背景音乐大小：</p>
                <input type="range" max="100" value="50" @change="setVolume($event)" />
            </div>
        </div>
        <div class="board white" v-show="myBill.show">
            <div class="server_list">账单</div>
            <div class="board bill_item">
                <dd class="board" v-for="(item, index) in myBill.list" :key="index">
                    <div class="user_acc bill_detail">
                        <span class="bold">类型：</span>
                        <span>{{myBill.list[index].type}}</span>
                    </div>
                    <div class="user_acc bill_detail">
                        <span class="bold">金额：</span>
                        <span>{{myBill.list[index].money}}</span>
                    </div>
                    <div class="user_acc bill_detail">
                        <span class="bold">时间：</span>
                        <span>{{myBill.list[index].time}}</span>
                    </div>
                </dd>
                <div class="board bill_num_tip" v-show="myBill.list.length == 0"></div>
                <a href="#" v-show="myBill.list.length > 0">
                    &gt;&gt;&nbsp;点击此处返回顶部&nbsp;&lt;&lt;
                </a>
            </div>
        </div>
        <button class="board sign_out" @click="signOut()">退出登录</button>
    </div>
</template>

<style scoped>
    @import "../css/Mine.css";
</style>

<script>
    export default {
        name: "Mine",
        data() {
            return {
                bgImg: "http://img12.360buyimg.com/img/s1500x886_jfs/t1/115726/22/13102/240974/5f18fb83Ee40e230b/d4f2a67087fd443a.png",
                userImg: "http://47.115.51.185/h5/img/logo2.47964cc3.png",
                headImgShow: true,
                headImgSwing: true,
                bgm: false,
                money: Number(localStorage.getItem("money：" + this.$root.$children[0].nowAccount.num)).toFixed(2),
                myBill: {
                    list: this.$root.$children[0].bill,
                    show: false,
                    index: 0
                },
                servers: [
                    {
                        name: "更换背景",
                        icon: "http://m.360buyimg.com/mobilecms/s120x120_jfs/t1/140012/8/1804/3641/5efbf318E38bd5dad/0db99d859ab16ce9.png.webp"
                    },
                    {
                        name: "更换头像",
                        icon: "http://m.360buyimg.com/babel/jfs/t1/32403/22/14829/3699/5cbf0674E04723693/caa83ce9b881cd24.png"
                    },
                    {
                        name: "头像显示",
                        icon: "http://m.360buyimg.com/mobilecms/s80x80_jfs/t1/11450/28/11433/7104/5c889da6Ebb724d42/0a53d60ab3603557.png.webp"
                    },
                    {
                        name: "头像动画",
                        icon: "http://m.360buyimg.com/mobilecms/s120x120_jfs/t1/115003/18/11199/3519/5efbf6eaEe59cb7f6/dab44230606cd112.png.webp"
                    },
                    {
                        name: "更改昵称",
                        icon: "http://img11.360buyimg.com/jdphoto/s80x80_jfs/t1/58091/32/5904/5093/5d3abd08Eee626e18/3375ebc9c9ef4465.png"
                    },
                    {
                        name: "背景音乐",
                        icon: "http://img12.360buyimg.com/img/s80x80_jfs/t1/160529/13/18012/6875/60741360E75f986d0/252b5f7a4e8ee8f0.png"
                    },
                    {
                        name: "余额充值",
                        icon: "http://m.360buyimg.com/mobilecms/s120x120_jfs/t1/131663/33/3380/3674/5efbf50fEf79cf314/af4b57d2383e605d.png.webp"
                    },
                    {
                        name: "我的账单",
                        icon: "http://img12.360buyimg.com/img/s80x80_jfs/t1/167238/16/17564/5351/607416d3Eb63bb5b9/34eba3e16cf8c656.png"
                    },
                    {
                        name: "注销账号",
                        icon: "http://img12.360buyimg.com/img/s80x80_jfs/t1/174254/26/4232/7803/6077a3c6Eab7dc8c6/d09b7c89dbc7b2b6.png"
                    }
                ],
                loginTime: sessionStorage.getItem("loginTime"),
                nickname: localStorage.getItem("nickname：" + this.$root.$children[0].nowAccount.num)
            }
        },
        mounted() {
            if (this.$root.$children[0].isLogin == false) {
                this.$router.push("/login")
            };
            if (localStorage.getItem("img：" + this.$root.$children[0].nowAccount.num) != null || localStorage.getItem("img：" + this.$root.$children[0].nowAccount.num) != undefined) {
                this.userImg = localStorage.getItem("img：" + this.$root.$children[0].nowAccount.num)
            };
            if (localStorage.getItem("bg：" + this.$root.$children[0].nowAccount.num) != null || localStorage.getItem("bg：" + this.$root.$children[0].nowAccount.num) != undefined) {
                this.bgImg = localStorage.getItem("bg：" + this.$root.$children[0].nowAccount.num)
            };
            document.getElementById("music").volume = 0.5;
            if (this.money == "" || this.money == null) {
                this.money = (0).toFixed(2)
            }
        },
        methods: {
            userNum() {
                let num = this.$root.$children[0].nowAccount.num;
                let num1 = num.slice(0,3);
                let num2 = num.slice(7,11);
                return num1 + "****" + num2
            },  //显示手机号码

            userTime() {
                let num = this.$root.$children[0].nowAccount.num;
                let time = localStorage.getItem("time：" + num);
                return time
            },  //显示注册时间

            signOut() {
                let q = confirm("确定要退出登录吗？");
                if (q == true) {
                    this.$root.$children[0].isLogin = false;
                    this.$root.$children[0].nowAccount.num = "";
                    this.$root.$children[0].nowAccount.pw = "";
                    this.$root.$children[0].nowAccount.time = "未知";
                    this.$root.$children[0].showToast("已退出登录");
                    this.$router.push("/login")
                }
            },  //退出登录按钮

            setImg(e) {
                let fn = e.path[0].files[0].name;
                let fs = Number(e.path[0].files[0].size);
                const that = this;
                if (fs > 2097152) {
                    alert("大小不能超过2MB");
                    return
                };
                if (/\.jpg$/i.test(fn) || /\.jpeg$/i.test(fn) || /\.gif$/i.test(fn) || /\.png$/i.test(fn) || /\.bmp$/i.test(fn)) {
                    let fileReader = new FileReader();
                    fileReader.readAsDataURL(e.path[0].files[0]);
                    fileReader.onload = () => {
                        let img = new Image();
                        img.src = fileReader.result;
                        if (img.naturalWidth / img.naturalHeight != 1) {
                            that.$root.$children[0].showToast("建议选择尺寸为1:1的图片，否则显示效果不佳")
                        };
                        localStorage.setItem("img：" + this.$root.$children[0].nowAccount.num, fileReader.result);
                        that.userImg = fileReader.result
                    }
                } else {
                    that.$root.$children[0].showToast("仅支持jpg、jpeg、gif、png、bmp格式");
                    return
                }
            },  //设置头像

            setBG(e) {
                let fn = e.path[0].files[0].name;
                const that = this;
                if (/\.jpg$/i.test(fn) || /\.jpeg$/i.test(fn) || /\.gif$/i.test(fn) || /\.png$/i.test(fn) || /\.bmp$/i.test(fn)) {
                    let fileReader = new FileReader();
                    fileReader.readAsDataURL(e.path[0].files[0]);
                    fileReader.onload = () => {
                        localStorage.setItem("bg：" + this.$root.$children[0].nowAccount.num, fileReader.result);
                        that.bgImg = fileReader.result
                    }
                } else {
                    that.$root.$children[0].showToast("仅支持jpg、jpeg、gif、png、bmp格式");
                    return
                }
            },  //设置背景

            setVolume(e) {
                document.getElementById("music").volume = (Number(e.path[0].value) / 100).toFixed(2)
            },  //设置BGM音量

            hideBill() {
                this.myBill.show = false
            },  //收起账单列表

            setFn(index) {
                if (index == 0) {
                    document.getElementById("set_head_bg").click()
                } else if (index == 1) {
                    document.getElementById("set_head_img").click()
                } else if (index == 2) {
                    if (this.headImgShow == 1) {
                        this.headImgShow = 0
                    } else {
                        this.headImgShow = 1
                    }
                } else if (index == 3) {
                    this.headImgSwing = !this.headImgSwing
                } else if (index == 4) {
                    let q = prompt("输入新的昵称：");
                    if (typeof q != "string" || q == "") {
                        this.$root.$children[0].showToast("修改未完成，请输入有效文字")
                    } else {
                        localStorage.setItem("nickname：" + this.$root.$children[0].nowAccount.num, q);
                        this.nickname = localStorage.getItem("nickname：" + this.$root.$children[0].nowAccount.num);
                        this.$root.$children[0].showToast("修改成功")
                    }
                } else if (index == 5) {
                    if (this.$root.$children[0].music.play == false) {
                        document.getElementById("music").play();
                        this.$root.$children[0].music.play = true;
                        this.bgm = true
                    } else {
                        document.getElementById("music").pause();
                        this.$root.$children[0].music.play = false;
                        this.bgm = false
                    }
                } else if (index == 6) {
                    let m = localStorage.getItem("money：" + this.$root.$children[0].nowAccount.num);
                    let q = prompt("输入需要充值的金额：");
                    if (q == null || Number(q).toString() == "NaN") {
                        this.$root.$children[0].showToast("请输入有效数字");
                        return
                    };
                    m = (Number(m) + Number(q)).toFixed(2);
                    localStorage.setItem("money：" + this.$root.$children[0].nowAccount.num, m);
                    this.money = localStorage.getItem("money：" + this.$root.$children[0].nowAccount.num);
                    let obj = {type: "充值", money: Number(q).toFixed(2), time: this.$root.$children[0].rgtTime()};
                    this.$root.$children[0].bill.unshift(obj);
                    this.$root.$children[0].showToast("充值成功")
                } else if (index == 7) {
                    this.myBill.show = !this.myBill.show
                } else if (index == 8) {
                    let q = confirm("注销账号会删除所以个人信息，确定要继续吗？");
                    if (q == true) {
                        let c = this.getVCode();
                        this.$root.$children[0].showToast("验证码：" + c);
                        console.log("【注销账号】本次验证码是：" + c);
                        let a;
                        setTimeout(() => {
                            a = prompt("请输入注销账号的验证码：");
                            if (a == null) {
                                this.$root.$children[0].showToast("操作已取消")
                            } else if (a == c) {
                                localStorage.removeItem("time：" + this.$root.$children[0].nowAccount.num);
                                localStorage.removeItem("bg：" + this.$root.$children[0].nowAccount.num);
                                localStorage.removeItem("nickname：" + this.$root.$children[0].nowAccount.num);
                                localStorage.removeItem("money：" + this.$root.$children[0].nowAccount.num);
                                localStorage.removeItem("img：" + this.$root.$children[0].nowAccount.num);
                                localStorage.removeItem(this.$root.$children[0].nowAccount.num);
                                this.$root.$children[0].nowAccount.num = "";
                                this.$root.$children[0].nowAccount.pw = "";
                                this.$root.$children[0].nowAccount.time = "未知";
                                this.$root.$children[0].isLogin = false;
                                this.$router.push("/login");
                                this.$root.$children[0].showToast("账号已注销")
                            } else {
                                this.$root.$children[0].showToast("验证码不正确")
                            }
                        }, 500)
                    }
                }
            },  //功能按钮

            getVCode() {
                let num = "";
                for (let i = 0; i < 6; i++) {
                    num = num + (Math.round(9 * Math.random())).toString()
                };
                return num
            },  //随机生成6位数字验证码
        },
        computed: {},
        components: {}
    }
</script>