<template>
    <div class="board page_cart_board">
        <h2 class="board cart_head">商品列表</h2>
        <ul class="cart_goods_list">
            <li v-for="(item, index) in cartList" :key="index" class="board">
                <div class="board cart_goods_img">
                    <img :src="cartList[index].img" width="100%" height="auto" />
                </div>
                <div class="board cart_goods_info">
                    <h2 class="board">{{cartList[index].name}}</h2>
                    <p class="board cart_goods_price">
                        <span>￥</span>
                        <span>{{cartList[index].price}}</span>
                    </p>
                    <div class="cart_goods_fn">
                        <button @click="reduceGoodsNum(index)">-</button>
                        <input type="text" v-model="cartList[index].num" @blur="numInput(index, $event)" />
                        <button @click="addGoodsNum(index)">+</button>
                        <input type="checkbox" v-model="cartList[index].select" @change="calc()" />
                    </div>
                </div>
            </li>
            <div class="board cart_num_tip" v-show="cartList.length == 0"></div>
        </ul>
        <div class="goods_deal_btn">
            <h3 class="board">
                <span>总计：</span>
                <span class="font_red">￥</span>
                <span class="font_red">{{total}}</span>
            </h3>
            <button class="board" @click="toPay()">立即支付</button>
        </div>
    </div>
</template>

<style scoped>
    @import "../css/Cart.css";
</style>

<script>
    export default {
        name: "Cart",
        data() {
            return {
                cartList: this.$root.$children[0].listFromCart,
                total: (0).toFixed(2)
            }
        },
        mounted() {
            document.getElementById("nav").style.display = "flex";
            if (this.$root.$children[0].isLogin == false) {
                this.$router.push("/login");
                return
            };
            if (this.cartList.length > 0) {
                let m = 0;
                for (let i = 0; i < this.cartList.length; i++) {
                    if (this.cartList[i].select == true) {
                        m = m + Number(this.cartList[i].price) * Number(this.cartList[i].num)
                    }
                };
                this.total = m.toFixed(2)
            }
        },
        methods: {
            reduceGoodsNum(i) {
                //数组删除元素方法：数组名.splice()，两个参数；
                //第一个参数是要开始删除的元素的下标
                //第二个是要删除元素的数量
                if (this.cartList[i].num <= 1) {
                    let q = confirm("确定从购物车中删除这件商品吗？");
                    if (q == true) {
                        this.cartList.splice(i, 1)
                    }
                } else if (this.cartList[i].num > 99) {
                    this.cartList[i].num = 99
                } else {
                    this.cartList[i].num = parseInt(this.cartList[i].num - 1)
                };
                this.calc()
            },  //数量减少

            addGoodsNum(i) {
                if (this.cartList[i].num >= 99) {
                    this.$root.$children[0].showToast("商品数量最多99");
                } else if (this.cartList[i].num < 1) {
                    this.cartList[i].num = 1
                } else {
                    this.cartList[i].num = parseInt(this.cartList[i].num + 1)
                };
                this.calc()
            },  //数量增加

            numInput(i, e) {
                if (e.path[0].value < 1) {
                    let q = confirm("确定从购物车中删除这件商品吗？");
                    if (q == true) {
                        this.cartList.splice(i, 1)
                    } else {
                        this.cartList[i].num = 1
                    }
                } else if (e.path[0].value > 99) {
                    this.$root.$children[0].showToast("商品数量最多99");
                    this.cartList[i].num = 99
                };
                this.cartList[i].num = parseInt(this.cartList[i].num);
                this.calc()
            },  //手动输入数量

            calc() {
                let t = 0;
                for (let i = 0; i < this.cartList.length; i++) {
                    if (this.cartList[i].select == true) {
                        t = t + (Number(this.cartList[i].price) * Number(this.cartList[i].num))
                    }
                };
                this.total = t.toFixed(2)
            },  //计算总价格

            toPay() {
                if (this.cartList.length < 1) {
                    this.$root.$children[0].showToast("购物车里没有商品")
                } else {
                    let arr = [];
                    let t = 0;
                    for (let i = 0; i < this.cartList.length; i++) {
                        if (this.cartList[i].select == true) {
                            arr.push(this.cartList[i]);
                            t = t + Number(this.cartList[i].price)
                        }
                    };
                    if (arr.length == 0) {
                        this.$root.$children[0].showToast("请勾选需要购买的商品")
                    } else if (Number(localStorage.getItem("money：" + this.$root.$children[0].nowAccount.num)) < t) {
                        this.$root.$children[0].showToast("当前余额不足，请前往充值")
                    } else {
                        this.$root.$children[0].orderList = arr;
                        this.$router.push("/order")
                    }
                }
            }  //支付按钮
        },
        computed: {},
        components: {}
    }
</script>