<template>
  	<div class="board">
    	<Search></Search>
    	<div class="board sort_bg">
      		<div class="sort_left">
        		<a :href="'#sort' + (index + 1)" v-for="(item, index) in category" :class="tabIndex == index ? 'sort_tab' : ' '" :key="index" @click="tabChange(index)">{{category[index].cate_name}}</a>
      		</div>
      		<ul class="sort_right">
        		<li v-for="(item, index) in category" :key="index" :id="'sort' + (index + 1)">
          			<div class="board sort_title">{{category[index].cate_name}}</div>
          			<ul class="board sort_list">
            			<li v-for="(child, i) in category[index].children" :key="i">
              				<img :src="category[index].children[i].pic" width="75%" height="auto" @click="toMore()" />
              				<h4 @click="toMore()">{{category[index].children[i].cate_name}}</h4>
            			</li>
          			</ul>
        		</li>
				<div class="loading_ball" v-show="category.length == 0"></div>
      		</ul>
    	</div>
 	</div>
</template>

<style scoped>
  	@import "../css/Sort.css";
</style>

<script>
  	import Search from '../components/Search.vue';  //搜索栏模块
  	import axios from '../../axios';
  
  	export default {
    	name: "Sort",
    	data() {
      		return {
        		category: [],
        		tabIndex: 0
      		}
    	},
    	beforeMount() {
      		document.body.scrollTop = 0
    	},
    	mounted() {
      		const that = this;
			window.scrollTo(0,0);
      		axios.get('http://47.115.51.185/api/category').then((res) => {
				console.log("分类：", res);
        		that.category = res.data.data;
        		that.scrollIndex()
			}).catch((err) => {
				console.log(err)
			})
   		},
    	methods: {
    	  	tabChange(i) {
    	    this.tabIndex = i
    		},
    		scrollIndex() {
   	     		const that = this;
        		if (that.category.length != 0) {
          			window.onscroll = () => {
            			let st = document.documentElement.scrollTop;
            			let ph = document.getElementById("app").offsetHeight - window.innerHeight;
            			that.tabIndex = parseInt(st / (ph / that.category.length))
          			}
        		}
      		},
			toMore() {
				this.$router.push("/more")
			}
    	},
    	computed: {},
    	components: {
      		Search
    	}
  	}
</script>