<template>
	<div class="board search_board">
    	<slot name="head_logo"></slot>
    	<div class="board search_bg">
      		<p>🔍</p>
      		<input type="search" :placeholder="search.kw[search.index]" class="goods_search" />
    	</div>
  	</div>
</template>

<script>
	import axios from "../../axios";
	export default {
  		name: "Search",
  		data() {
   			 return {
      			logo: "",
      			search: {
        			kw: ["吸尘器", "加湿器", "马桶", "热水器"],
        			index: 0,
        			in: undefined,
					out: 4000
      			}
    		}
  		},
  		mounted() {
    		this.search.in = setInterval(() => {
				if (this.search.index >= this.search.kw.length - 1) {
					this.search.index = 0
				} else {
					this.search.index = this.search.index + 1
				}
    		}, this.search.out)
  		},
  		props: {
    		msg: String
  		}
	};
</script>

