<template>
    <div class="board more_frame">
        <Search></Search>
        <div class="board pop_kw">
            <ul class="board">
                <li v-for="(item, index) in kw" :key="index">{{kw[index]}}</li>
            </ul>
        </div>
        <div class="board">
            <h3 class="board goods_cm_title">优品推荐</h3>
            <ul class="board more_cm_goods">
                <li class="board" v-for="(item, index) in cmList" :key="index">
                    <div class="board more_cm_img">
                        <img :src="cmList[index].image" width="100%" height="auto" :value="cmList[index].id" @click="toDetail($event)" />
                    </div>
                    <p class="board" :value="cmList[index].id" @click="toDetail($event)">{{cmList[index].store_name}}</p>
                    <h4 :value="cmList[index].id" @click="toDetail($event)">￥{{cmList[index].price}}</h4>
                </li>
            </ul>
        </div>
        <div class="board" id="more_cm">
            <h3 class="board goods_cm_title">更多推荐</h3>
            <ul class="board more_cm_goods">
                <li class="board" v-for="(item, index) in moreList" :key="index">
                    <div class="board more_cm_img">
                        <img :src="moreList[index].image" width="100%" height="auto" :value="moreList[index].id" @click="toDetail($event)" />
                    </div>
                    <p class="board" :value="moreList[index].id" @click="toDetail($event)">{{moreList[index].store_name}}</p>
                    <h4 :value="moreList[index].id" @click="toDetail($event)">￥{{moreList[index].price}}</h4>
                </li>
            </ul>
        </div>
    </div>
</template>

<style scoped>
    @import "../css/More.css";
</style>

<script>
    import Search from '../components/Search.vue';  //搜索栏模块
    import axios from "../../axios";  //axios
    export default {
        data() {
            return {
                kw: [],
                cmList: [],
                moreList: []
            }
        },
        mounted() {
            const that = this;
            axios.get('http://47.115.51.185/api/search/keyword').then((res) => {
				console.log("keywords：", res);
                that.kw = res.data.data
			}).catch((err) => {
				console.log(err)
			});
            axios.get('http://47.115.51.185/api/product/detail/7').then((res) => {
				console.log("更多商品：", res);
                that.cmList = res.data.data.similarity;
                that.moreList = res.data.data.good_list
			}).catch((err) => {
				console.log(err)
			})
            
        },
        methods: {
            toDetail(e) {
                sessionStorage.setItem("goodsID", e.path[0].getAttribute("value"));
                this.$router.push("/detail")
            }
        },
        computed: {},
        components: {
            Search
        }
    }
</script>