<script>
	export default {
    	name: "App",
    	data() {
      		return {
        		isLogin: false,
        		nowAccount: {
					    num: "",
					    pw: "",
					    time: "未知"
				    },
            music: {
              url: "https://aod.cos.tx.xmcdn.com/group19/M00/BA/6E/wKgJK1e-4o_SrvA1AAcJuQnzv2M064.m4a",
              play: false
            },
        		navIndex: 0,
            toast: {
              content: "",
              show: 0,
              out: undefined
            },
            loginTime: sessionStorage.getItem("loginTime"),
            listFromCart: [],
            orderList: [],
            bill: [],
            likeList: []
      		}
    	},
    	mounted() {
			document.querySelector("title").innerHTML = "Cool City";
      		if (sessionStorage.getItem("nowAccount") != null || sessionStorage.getItem("nowAccount") != undefined) {
				  this.isLogin = true
				  this.nowAccount.num = sessionStorage.getItem("nowAccount");
      		}
    	},
    	methods: {
        //若要显示弹窗则调用该函数
        showToast(content, time) {
          //第一个参数是弹窗内容（string）
          //第二个参数是显示时长（number），1000是1秒，2000是2秒...以此类推
          //显示时长最少1000，不填则默认是2000
          this.toast.content = content;
          if (typeof time != "number") {
            time = 2000
          } else if (time < 1000) {
            time = 1000
          } else if (time.toString() == "NaN") {
            time = 2000
          };
          if (this.toast.out == undefined) {
            this.toast.show = 1;
            this.toast.out = setTimeout(() => {
              this.toast.show = 0;
              this.toast.out = undefined
            }, time)
          }
        },

        rgtTime() {
          let ye = (new Date()).getFullYear();
          let mo = (new Date()).getMonth() + 1;
          let da = (new Date()).getDate();
          let ho = (new Date()).getHours();
          ho < 10 ? ho = '0' + ho : ho = ho;
          let mi = (new Date()).getMinutes();
          mi < 10 ? mi = '0' + mi : mi = mi;
          let se = (new Date()).getSeconds();
          se < 10 ? se = '0' + se : se = se;
          return `${ye}年${mo}月${da}日 ${ho}时${mi}分${se}秒`
        },  //注册时间
      },
    	components: {}
	}
</script>
