<template>
    <div class="board order_frame">
        <h2 class="board cart_head">确认订单</h2>
        <div class="board white order_board">
            <div class="user_acc">
                <span class="bold">收货人：</span>
                <span class="c999">{{addressInfo.name}}</span>
            </div>
            <div class="user_acc">
                <span class="bold">联系电话：</span>
                <span class="c999">{{addressInfo.phone}}</span>
            </div>
            <div class="user_acc">
                <span class="bold">收货地址：</span>
                <span class="c999">{{addressInfo.adr}}</span>
            </div>
        </div>
        <button class="board save_address" @click="setThisAddress(true)" v-show="addressInfo.show == false">修改收货信息</button>
        <div class="board white" v-show="addressInfo.show">
            <div class="server_list">修改收货信息</div>
            <input type="text" class="set_address" v-model="addressInfo.name" placeholder="填写收货人" />
            <input type="text" class="set_address" v-model="addressInfo.phone" placeholder="填写联系电话" />
            <input type="text" class="set_address" v-model="addressInfo.adr" placeholder="填写收货地址" />
            <button class="board save_address" @click="setThisAddress(false)">保存修改</button>
        </div>
        <ul class="board white list_pay">
            <div class="server_list">订单</div>
            <li class="board" v-for="(item, index) in listToPay" :key="index">
                <div class="board pay_img">
                    <img :src="listToPay[index].img" width="100%" height="auto" />
                </div>
                <div class="board pay_item_info">
                    <h3 class="board">{{listToPay[index].name}}</h3>
                    <h4 class="board">￥{{listToPay[index].price}}</h4>
                    <p>数量：{{listToPay[index].num}}</p>
                </div>
            </li>
        </ul>
        <div class="order_total_pay">
            <p>
                <span>共计：</span>
                <span class="red">￥{{totalPay}}</span>
            </p>
            <button @click="submitOrder()">提交订单</button>
        </div>
    </div>
</template>

<style scoped>
    @import "../css/Order.css";
</style>

<script>
    export default {
        name: "Order",
        data() {
            return {
                addressInfo: {
                    show: false,
                    name: "",
                    phone: "",
                    adr: ""
                },
                listToPay: this.$root.$children[0].orderList,
                totalPay: 0.00
            }
        },
        mounted() {
            let m = 0;
            for (let i = 0; i < this.listToPay.length; i++) {
                m = m + Number(this.listToPay[i].price) * Number(this.listToPay[i].num)
            };
            this.totalPay = m.toFixed(2)
        },
        methods: {
            setThisAddress(n) {
                if (n == false) {
                    if (this.addressInfo.name.length < 1) {
                        this.$root.$children[0].showToast("收货人不能为空");
                        return
                    };
                    if (this.addressInfo.phone.length < 1) {
                        this.$root.$children[0].showToast("电话号码不能为空");
                        return
                    };
                    if (this.addressInfo.adr.length < 1) {
                        this.$root.$children[0].showToast("收货地址不能为空");
                        return
                    }
                };
                this.addressInfo.show = n
            },  //修改收货信息按钮

            submitOrder() {
                if (this.addressInfo.name.length < 1) {
                    this.$root.$children[0].showToast("收货人不能为空");
                    return
                };
                if (this.addressInfo.phone.length < 1) {
                    this.$root.$children[0].showToast("电话号码不能为空");
                    return
                };
                if (this.addressInfo.adr.length < 1) {
                    this.$root.$children[0].showToast("收货地址不能为空");
                    return
                };
                if (Number(localStorage.getItem("money：" + this.$root.$children[0].nowAccount.num)) < Number(this.totalPay)) {
                    this.$root.$children[0].showToast("余额不足，请充值");
                    return
                };
                let b = Number(localStorage.getItem("money：" + this.$root.$children[0].nowAccount.num)) - this.totalPay;
                localStorage.setItem("money：" + this.$root.$children[0].nowAccount.num, b);
                let obj = {type: "购物", money: this.totalPay, time: this.$root.$children[0].rgtTime()};
                this.$root.$children[0].bill.unshift(obj);
                this.$root.$children[0].showToast("谢谢惠顾");
                this.$root.$children[0].listFromCart = [];
                this.$root.$children[0].orderList = [];
                this.$router.push("/mine")
            }
        },
        computed: {},
        components: {}
    }
</script>